import { useEffect, useState } from "react";
import { saveToCache, getFromCache } from "../utils/cache";

const DATA_URL = "/git/data.json";

export function useSubjects() {
  const [data, setData] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const cached = getFromCache("subjects");

    if (cached) {
      setData(cached);
      setLoading(false);
    }

    if (navigator.onLine) {
      fetch(DATA_URL)
        .then((res) => res.json())
        .then((json) => {
          saveToCache("subjects", json);
          setData(json);
          setLoading(false);
        });
    }
  }, []);

  return { data, loading };
}
